<template>   
    <v-container >
                    <v-row  justify="center">
                        <v-col cols="8">
                            <v-card>
                                <v-row>
                                    <v-col cols="4">
                                    </v-col>
                                    <v-col cols="5" >
                                        <div class="header" >
                                            <h1>Profile</h1>
                                        </div>
                                        <div class="form">
                                            <v-text-field
                                                :disabled="edit ? false : true"
                                                v-model="form.name"
                                                label="Name"
                                                filled
                                                rounded
                                            ></v-text-field>
                                            <v-menu
                                            v-model="menu2"
                                            :close-on-content-click="false"
                                            :nudge-right="40"
                                            transition="scale-transition"
                                            offset-y
                                            min-width="290px"
                                            >
                                            <template v-slot:activator="{ on }">
                                            <v-text-field
                                                :disabled="edit ? false : true"
                                                v-model="form.birthdate"
                                                label="Birthdate*"
                                                prepend-inner-icon="mdi-calendar"
                                                readonly
                                                filled
                                                rounded
                                                v-on="on"
                                            ></v-text-field>
                                            </template>
                                            <v-date-picker color="yellow" v-model="form.birthdate" @input="menu2 = false"></v-date-picker>
                                        </v-menu>
                                        <v-select
                                            :disabled="edit ? false : true"
                                            v-model="form.gender"
                                            :items="items"
                                            label="Gender*"
                                            filled
                                            rounded
                                            dense
                                        ></v-select> 
                                        <v-text-field
                                        :disabled="edit ? false : true"
                                                v-model="form.address"
                                                label="Address"
                                                filled
                                                rounded
                                            ></v-text-field> 
                                            <v-text-field
                                            :disabled="edit ? false : true"
                                                v-model="form.email"
                                                label="Email"
                                                filled
                                                rounded
                                            ></v-text-field>
                                            <v-checkbox v-model="edit" style="margin-top : -10px">
                                                <template v-slot:label>
                                                    <div>
                                                        Edit Data
                                                    </div>
                                                </template>
                                                </v-checkbox>
                                                <v-btn :hidden="edit ? false : true" @click="sendData()" block color="yellow" rounded class="elevation-0" height=40>Update Profile</v-btn>
                                            
                                        </div>
                                    </v-col>
                                    
                                </v-row>
                            </v-card>
                        </v-col>
                    </v-row>
                    <v-snackbar       
                    v-model="snackbar" 
                    :color="color"       
                    :multi-line="true"       
                    :timeout="3000"     
                    >       
                    {{ text }}       
                    <v-btn         
                     dark         
                     text         
                     @click="snackbar = false"       
                    >         
                        Close       
                    </v-btn>     
                </v-snackbar>   
                </v-container>       
</template> 
<script> 
import { log } from 'util'
export default {    
    data () {       
        return {  
            edit: false,
            menu2: false,
            show: false,      
            show1: false,      
            show2: false,      
            dialog: false,  
            pass: false,       
            keyword: '', 
            items: ['Male', 'Female'],        
            users: [],         
            snackbar: false,          
            color: null,         
            text: '',          
            load: false, 
            form: {            
                name : '',  
                gender: '',         
                address: '',         
                email : '',    
                birthdate:''        
            },         
            user : new FormData,         
            typeInput: 'new',        
            errors : '',         
            updatedId : '',       
        }     
    },     
    methods:{           
        sendData(){             
            this.user.append('name', this.form.name);             
            this.user.append('birthdate', this.form.birthdate);             
            this.user.append('gender', this.form.gender);             
            this.user.append('address', this.form.address);             
            this.user.append('email', this.form.email);         
            var uri =this.$apiUrl + '/User/register/' + localStorage.getItem('id')             
            this.load = true             
            this.$http.post(uri,this.user).then(response =>{               
                this.snackbar = true; //mengaktifkan snackbar               
                this.color = 'green'; //memberi warna snackbar               
                this.text = response.data.message; //memasukkan pesan ke snackbar               
                this.load = false;                 
                 //mengambil data user               
                          
            }).catch(error =>{               
                this.errors = error               
                this.snackbar = true;               
                this.text = 'Try Again';               
                this.color = 'red';               
                this.load = false;           
            }) 
            this.edit = false
            this.getData();        
        },         
        
        editHandler(){          
            var url = this.$apiUrl + '/User/data/' +localStorage.getItem('id');
            this.$http.get(url).then(response =>{
                if(response.data.id){
                    this.form.name = response.data.name;           
                    this.form.email = response.data.email;           
                    this.form.birthdate = response.data.birthdate;
                    this.form.gender = response.data.gender;           
                    this.form.address = response.data.address; 
                }else{
                    alert('gagal ')
                }
            })          
                 
        },
        
        deleteData(deleteId){ //mengahapus data
            // var config = {
            //     headers: {
            //         Authorization: 'Bearer ' + localStorage.getItem('token')
            //     }
            // }              
            var uri = this.$apiUrl + '/User/' + deleteId; //data dihapus berdasarkan id 
            this.$http.delete(uri).then(response =>{                 
                this.snackbar = true;                 
                this.text = response.data.message;                 
                this.color = 'green'                 
                this.deleteDialog = false;                 
                this.getData();             
            }).catch(error =>{                 
                this.errors = error                 
                this.snackbar = true;                 
                this.text = 'Try Again';                 
                this.color = 'red'; 
            })         
        },
        
        setForm(){             
            if (this.typeInput === 'new') {               
                this.sendData()             
            } else {               
                this.updateData()             
            }         
        },         
        resetForm(){             
            this.form = {               
                name : '',               
                email : '',               
                password : '',
                gender: '',         
                address: '',         
                email : '',           
                password : ''             
            }
            this.dialog = false
            this.pass = false           
        }     
    },     
    mounted(){         
        this.editHandler();     
        }, 
    } 
</script> 
  
<style>
.header{
    margin-left: 170px;
    color: #000000;
    /* margin-bottom: 690px;
    margin-right: 654px; */
}
#title{
    font-family: Roboto;
}
#tagline{
    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 33px;
}
.form{
    margin-left: -50px;
    margin-top: 100px;
    margin-bottom: 120px;
    color: rgba(0, 0, 0, 0.54);
}
</style>